
/*http://facebook.github.io/react/docs/component-specs.html*/
var React = require('react');
var Router = require('react-router');
var Reflux = require('reflux');
var Link = Router.Link;
var FilterActions = require('../../actions/filterActions.js');

var FilterItem = React.createClass({displayName: "FilterItem",

    _onClickApply: function(event) {     
        FilterActions.triggerFilterApply();
    },

    _onClickReset: function(event) {     
        FilterActions.triggerFilterReset();
    },

    render: function() {
        var item = this.props.data;       
        return(
            React.createElement("div", {class: "button-pane"}, 
                React.createElement("button", {type: "button", className: "btn btn-apply", role: "button", onClick: this._onClickApply}, "Apply"), 
                React.createElement("button", {type: "button", className: "btn btn-apply", role: "button", onClick: this._onClickReset}, "Reset")
            )            
        );
    }
});

module.exports = FilterItem;