'use strict';

var React = require('react');
var FilterItem = require('./filterItem.jsx');

var FilterGroup = React.createClass({displayName: "FilterGroup",

    _onItemChanged: function(event) {  
        this.props.onItemChanged(this.props.filterType, event.target.value, event.target.checked);
    },

    render: function() {
        var items = this.props.items;  
        var filterType = this.props.filterType;  
        var self = this;
        return(
            React.createElement("div", {className: "filter-list-container"}, 
                React.createElement("ul", {className: "filter-list"}, 
                    
                        items.map(function(item){ 
                            if (!item.hide){   
                                return React.createElement("li", {key: item.id}, 
                                    React.createElement(FilterItem, {id: item.id, name: item.name, selected: item.selected, filterType: filterType, onItemChanged: self._onItemChanged})
                                );
                            }
                        })
                    
                )
            )
            );
    }
});

module.exports = FilterGroup;