'use strict';
var React = require('react');
var Reflux = require('reflux');
var Link = require('react-router').Link;
var Message=require('../lanMessage.jsx');
var ExternalLayersActions=require('../../actions/externalLayersActions.js');

/*The button to add the layer to the map*/
var AddButton= React.createClass({displayName: "AddButton",
	handleAdd:function(){
		ExternalLayersActions.addLayerToMap(this.props.layer);
	},

	render: function() {
		return(React.createElement("button", {className: "btn btn-xs btn-info", onClick: this.handleAdd}, "ADD"));	
	}

});


/*The result list it should be an infinite scroll maybe */
var List= React.createClass({displayName: "List",
	render: function() {
		return(React.createElement("div", {className: "scrollable small"}, 
		 
			this.props.layers.map(function(l){
				return(
					React.createElement("div", null, 
					React.createElement("p", null, l.title), 
					React.createElement("div", null, 
					React.createElement("img", {src: "http://www.arcgis.com/sharing/content/items/"+l.id+"/info/"+l.thumbnail}), 
					React.createElement("b", null, l.type), 

					React.createElement(AddButton, {layer: l})
					)
					))
			})
			));	
	}	
});


var Search=React.createClass({displayName: "Search",

	handleCLick:function(){
		this.props.onSearch(this.refs.search_input.getDOMNode().value);
	},

	render: function() {
		return   (  
			React.createElement("div", null, 
				React.createElement("p", null, React.createElement(Message, {message: "layers.search"})), 
				React.createElement("input", {type: "text", placeholder: "Search layer", ref: "search_input"}), " ", React.createElement("button", {onClick: this.handleCLick}, "GO")
			)
			);
	}	
});

/*
  Root Element input text + search list

  */
  module.exports  = React.createClass({displayName: "exports",
  	render: function() {
  		return (  
  			React.createElement("div", null, 
	  			React.createElement(Search, {onSearch: this.props.onSearch}), 
	  			React.createElement("div", null, 	
	  				this.props.layers?  React.createElement(List, {layers: this.props.layers}):null
	  			)
	  		)
  			);
  	}
  });