'use strict';

var React = require('react');
var Reflux = require('reflux');

module.exports  = React.createClass({displayName: "exports",


  render: function() {

    if (!this.props.feature){
      return (React.createElement("p", null))
    }
    return (
      React.createElement("div", null, 
       React.createElement("div", {className: "panel panel-info"}, 
          React.createElement("div", {className: "panel-heading"}, 
            React.createElement("h3", {className: "panel-title"}, 
                 this.props.feature.properties.municipality?this.props.feature.properties.municipality:this.props.feature.properties.department
            )
        ), 
         React.createElement("div", {className: "panel-body"}, 
               React.createElement("p", null, "actividades ", this.props.feature.properties.actividades), 
                React.createElement("p", null, "U$D ", this.props.feature.properties.fundingUS), 
                 React.createElement("p", null, "Number of projects ", this.props.feature.properties.projects)
                  
          )
      )
      )
      );
  }

});
