'use strict';

var React = require('react/addons')
var Reflux = require('reflux');
var Link = require('react-router').Link;

var ReactTransitionGroup = React.addons.TransitionGroup;
var ReactCSSTransitionGroup = React.addons.CSSTransitionGroup;


var BarContent = React.createClass({displayName: "BarContent",
  render: function() {
    return React.createElement("div", {className: "slider_content"}, this.props.children);
  }
});


module.exports  = React.createClass({displayName: "exports",
  getInitialState: function() {
    return {
      open: false
    };
  },
  
  componentWillMount: function() {
    this.setState({
      open: this.props.open
    });
  },
  
  componentWillReceiveProps: function(props) {
    this.setState({
      open: props.open
    });
  },
  
  open: function() {
    this.setState({
      open: true
    });
  },

  close: function() {
    this.setState({
      open: false
    });
  },

  toggle: function() {
    this.setState({
      open: !this.state.open
    });
  },
  
  render: function() {
    return (
      React.createElement("div", {id: "sidebar-wrapper"}, 
      React.createElement("div", {className: "panel-toggle"}, 
      React.createElement("i", {className: "fa fa-bars", onClick: this.state.open?this.close:this.open})
      ), 
      React.createElement(ReactCSSTransitionGroup, {transitionName: "slider", component: "div"}, 
      this.state.open && React.createElement(BarContent, {key: "content"}, " ", this.props.children, " ")
      )
      )
      );
  } 

});
