
'use strict';
var React = require('react')
var LanActions=require('../actions/lanActions.js');
var LanStore=require('../stores/lanStore.js');
var Reflux = require('reflux');
var LanStore=require('../stores/lanStore.js');


module.exports = React.createClass({displayName: "exports",

	mixins: [Reflux.connect(LanStore, 'lan')],

	handleLocaleChange:function(){
		var locale=this.refs.lanSelector.getDOMNode().value;
		LanActions.changeLocale(locale);
	},
	

	render: function() {
		return (
			React.createElement("select", {ref: "lanSelector", onChange: this.handleLocaleChange}, 
				React.createElement("option", {value: "en", selected: this.state.lan.locale=='en'?'selected':''}, "English"), 
				React.createElement("option", {value: "es", selected: this.state.lan.locale=='es'?'selected':''}, "Español")
			)
			);
	}
});


